<template>
  <div>
    <Navigation :no="nodispose"></Navigation>
    <div class="main">
      <div class="option" v-for="item in noticeList">
        <div class="content">
          <span v-text="item.content"></span>
          <!--判断消息的处理状态-->
          <template v-if="item.status === '0'">
            <span class="tool" @click="handleNotice(item.logid, 1)">同意</span><span class="tool" @click="handleNotice(item.logid, 2)">拒绝</span>
          </template>
        </div>
        <div class="date" v-text="item.createtime"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import API from '../api.js'
  import Navigation from '../components/navigation.vue'
  export default {
    components: {
      Navigation
    },
    data () {
      return {
        noticeList: [],
        nodispose: false
      }
    },
    created () {
      this.getNoticeList()
    },
    methods: {
      getNoticeList () {
        API.GetNotices({userid: this.$root.userid}).then(response => {
          if (!response.length) { return }
          this.noticeList = response
        }).catch(err => {
          console.log(err)
        })
      },
      handleNotice (id, status) {
        API.HandleNotice({userid: this.$root.userid, noticeid: id, status: status}).then(response => {
          if (!response) { return }
          this.getNoticeList()
        }).catch(err => {
          console.log(err)
        })
      }
    }
  }
</script>
<style scoped>
  .main {
    width: 80%;
    margin: 0 auto;
    margin-top: 100px;
  }
  .option{
    position: relative;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    line-height: 1.7; 
  }
  .option .content .tool{
    display: inline-block;
    padding: 0 5px;
    background: #20a0ff;
    border: 1px #0190fe solid;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
  }
  .option .content .tool.active{
    display: inline-block;
    padding: 0 5px;
    background: #ccc;
    border: 1px #ccc solid;
    color: #000;
    font-size: 14px;
    cursor: default;
  }
</style>



