<template>
  <div id="userInfo">
    <Navigation></Navigation>
    <div class="center">
      <div class="btn" @click="edit = true" :class="{active: edit}">编辑</div>
      <div class="option"><label>用户名：</label><input type="text" v-model="userInfo.s_username" :readonly="!edit" :class="{edit: edit}"/></div>
      <div class="option"><label>性别：</label>
        <select v-model="userInfo.s_sex" :readonly="!edit" :class="{edit: edit}" :disabled="disabled = !edit">
          <option value="1">男</option> 
          <option value="2">女</option>  
        </select>
      </div>
      <div class="option"><label>手机号：</label><input type="text" v-model="userInfo.s_cellphone" readonly/></div>
      <div class="option"><label>Email：</label><input type="text" v-model="userInfo.s_userEmail" :readonly="!edit" :class="{edit: edit}"/></div>
      <div class="option"><label>工作单位：</label><input type="text" v-model="userInfo.s_workunit" :readonly="!edit" :class="{edit: edit}"/></div>
      <div class="option"><label>工作科室：</label><input type="text" v-model="userInfo.s_department" :readonly="!edit" :class="{edit: edit}"/></div>
      <div class="option"><label>职称：</label><input type="text" v-model="userInfo.s_joblevel" :readonly="!edit" :class="{edit: edit}"/></div>      
      <div class="tools" v-if="edit">
        <span class="tool" @click="editInfo">保存</span>
        <span class="tool" @click="edit=false">取消</span>
      </div>
    </div>
  </div>
</template>
<script>
  import Navigation from '../components/navigation.vue'
  import API from '../api.js'
  export default {
    data () {
      return {
        userInfo: {},
        edit: false
      }
    },
    components: {
      Navigation
    },
    created () {
      API.GetUserInfo({userid: window.sessionStorage.getItem('userid')}).then((response) => { // success callback
        this.userInfo = response[0]
      }).catch((err) => {
        window.alert(err)
      })
    },
    methods: {
      editInfo () {
        API.EditUserInfo({userInfo: JSON.stringify({'s_userid': this.userInfo.s_userid, 's_username': this.userInfo.s_username, 's_sex': this.userInfo.s_sex, 's_cellphone': this.userInfo.s_cellphone, 's_userEmail': this.userInfo.s_userEmail, s_joblevel: this.userInfo.s_joblevel, 's_workunti': this.userInfo.s_workunti, 's_department': this.userInfo.s_department})}).then((response) => {
          this.edit = false
        }).catch((err) => {
          window.alert(err)
        })
      }
    }
  }
</script>
<style scoped>
  .center{
    width: 40%;
    margin: 0 auto;
    margin-top: 120px;
    font-size: 14px; 
    position: relative;
    padding: 20px;
    background: #fff;
    border: 1px #ccc solid;
  }
  .btn{
    position: absolute;
    right: 0px;
    top: -30px;
    border: 1px solid #aaa;
    padding: 0 10px;
    cursor: pointer;
    border-radius: 3px;
  }
  .btn.active, .btn:hover{
    background: #20a0ff;
    border-color: #20a0ff;
    color: #fff; 
  }
  .option{
    margin: 5px;
  }
  .option input{
    border: none;
    background: #fff;
    border: 1px #fff solid;
    padding: 0 5px;
    margin-left: 5px;
    font-size: 14px;
  }
  .option input.edit, .option select.edit{
    border: 1px #eee solid;
    border-radius: 3px;
  }
  .option select{
    width: 150px;
    border: none;
    background: #fff;
    border: 1px #fff solid;
    padding: 5px;
    margin-left: 5px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    color: #000;
  }
  .tools{
    width: 100%;
    text-align: right;
    cursor: pointer;
  }
  .tool{
    border: 1px solid #aaa;
    padding: 3px 10px;
    cursor: pointer;
    border-radius: 3px;
  }
</style>


