<template>
  <div class="nav">
    <div class="flex-left">
      <img src="../assets/logo.png" alt="Medicayun" class="logo">
      <router-link to='/home'>首页</router-link>
      <router-link to='/notification'>通知<span v-if="nodispose"></span></router-link>
      <router-link to='/custom_form'>自定义form</router-link>
    </div>
    <div class="flex-right">
      <div class="user" @mouseenter="showDropdown = true" @mouseleave="showDropdown = false">
        <router-link to='/userInfo' style="padding-right: 32px;"> 
          <template v-if="userImgFlag">
            <img src="../assets/logo.png" alt="userImg" class="user-img">
          </template>
          <div class="user-img" v-else></div> 
        </router-link>
        <div class="nav-dropdown" v-if="showDropdown">
          <div class="my-center">
            <router-link to='/userInfo' style="color: #000">
              <div class="arrow"></div>
              <div class="user-img" style="background: rgb(70, 141, 241)"> 
              </div>
              <div class="user-info">
                <div class="user-name">{{ userInfo.s_username }}</div>
                <div class="user-work-unit">{{ userInfo.s_job }}</div>
              </div>
            </router-link>
          </div>
          <div class="action">
            <a href="#">功能设置</a>
            <a href="#">帮助中心</a>
          </div>
          <div class="exit-btn">
            <MButton type="gray" @click="exit">退出</MButton>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import MButton from '../components/button.vue'
  export default {
    name: 'Nav',
    data () {
      return {
        userInfo: {},
        userImgFlag: false, // 是否有上传用户头像
        showDropdown: false,
        nodispose: false // 新消息提醒
      }
    },
    components: {
      MButton
    },
    created () {
      // this.username = window.sessionStorage.getItem('username')
      this.getNoticeList()
      API.GetUserInfo({userid: this.$root.userid}).then(response => {
        this.userInfo = response[0]
      }).catch((err) => {
        window.alert(err)
      })
    },
    methods: {
      // 获取通知列表
      getNoticeList () {
        API.GetNotices({userid: this.$root.userid}).then(response => {
          if (!response.length) { return }
          for (let n in response) {
            if (n.status === '0') {
              this.nodispose = true
              break
            }
          }
        }).catch(err => {
          window.alert(err)
        })
      },
      exit () {
        this.$router.push('/login')
        window.sessionStorage.setItem('userid', '')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 导航条固定定位 */
  .nav {
    background: rgb(70, 141, 241);
    width: 100%;
    margin: 0 auto;
    display: flex;
    height: 64px;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    border-bottom: 1px solid rgb(227,227,227);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
  }
  
  .logo {
    width: 45px;
    height: 45px;
    margin: 0 32px;
  }

  .flex-left, .flex-right, .my-center, a,.user{
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
  }

  a {
    padding: 0 20px;
    font-size: 16px;
    cursor: pointer;
  } 
  .router-link-active{
    color: #fff;
  }
  a:hover {
    color: #fff;
  }

  .user{
    position: relative;
    cursor: pointer;
  }
  .user-img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: #fff;
    text-align: center;
    line-height: 32px;
  }
  .nav-dropdown {
    position: absolute;
    right: 20px;
    top: 58px;  
    border: 1px solid rgb(204, 204, 204);
    width: 228px;
    background: #fff;
  }
  .arrow {
    position: absolute;
    top: -16px;
    right: 19px;
    width: 0;
    height: 0;
    border: 8px solid transparent;
    border-bottom-color: rgb(204, 204, 204);
  }

  .arrow:after {
    content: " ";
    position: absolute;
    top: -5px;
    right: -7px;
    display: block;
    width: 0;
    height: 0;
    border: 7px solid transparent;
    border-bottom-color: rgb(250, 250, 250);
  } 

  .my-center{
    height: 102px;
    padding-left: 24px;
    padding-right: 12px;
    border-bottom: 2px solid rgb(227, 227, 227);
    background: rgb(250, 250, 250);
  } 
  .user-name {
    font-size: 18px;
    height: 24px;
    line-height: 24px;
  }
  .user-info {
    margin-left: 12px;
  }
  .user-work-unit {
    overflow : hidden;
    width: 140px;
    font-size: 14px;
    max-height: 36px;
    min-height: 18px;
    line-height: 18px;
  }

  .action{
    font-size: 16px;
    color: rgb(68, 68, 68);
    line-height: 30px;
    margin-left: 24px;
    margin-right: 12px;
    padding: 12px;
    border-bottom: 1px solid rgb(227, 227, 227);
  }
  .action a:hover{
    color: rgb(70, 141, 241)
  }
  .exit-btn {
    padding: 12px;
    text-align: right;
  }
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-active {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



