<template>
  <div id="home">
    <Navigation></Navigation>
    <div class="content">
      <div class="tools">
        <MButton class="btn" :type="mytype" @click="getMyProjects">我的项目</MButton>
        |
        <MButton class="btn" :type="alltype" @click="getAllProjects">所有公开的项目</MButton>
      </div>
      <!--项目卡片列表-->
      <div class="card" v-for="item in projectInfo">
        <router-link :to="'/home/projectid/' + item.u_projectID + '/all_patients'">
          <div class="text">
            <h3 v-text="item.u_projectName"></h3>
            <p v-text="item.u_projectMem"></p>
            <p class="project-num"><i></i></p>
          </div>
        </router-link>
        <div class="add-project-status">
          <span>已加入项目</span>
        </div>
      </div>
      <div class="no-project-hint" v-if="projectInfo.length === 0 && mytype === 'blue'">
        <p>您好，目前您还没有参加任何项目！</p>
        <p><span @click="getAllProjects">点击查看</span>所有公开项目</p>
      </div>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import Navigation from '../components/navigation.vue'
  import MButton from '../components/button.vue'
  export default {
    name: 'Home',
    components: {
      Navigation,
      MButton
    },
    data () {
      return {
        projectInfo: [],
        mytype: 'blue',
        alltype: 'gray'
      }
    },
    created () {
      this.getMyProjects()
    },
    methods: {
      getMyProjects () {
        this.projectInfo = []
        this.mytype = 'blue'
        this.alltype = 'gray'
        API.GetMyProjects({userid: this.$root.userid}).then(response => {
          this.projectInfo = response
        }).catch(err => {
          console.log(err)
        })
      },
      getAllProjects () {
        this.projectInfo = []
        this.mytype = 'gray'
        this.alltype = 'blue'
        /* API.GetAllProjects({userid: this.$root.userid}).then(response => {
          this.projectInfo = response
        }).catch(err => {
          console.log(err)
        }) */
      }
    }
  }
</script>
<style scoped>
  #home .content {
    width: 80%;
    margin: 100px auto 0;
  }
  /* 按钮工具 */
  .content .tools {
    text-align: center;
    margin: 50px 0 20px;
  }
  .content .tools .btn{
    margin: 0 20px;
  }
  /* 项目卡片 */
  .no-project-hint {
    text-align: center;
    color: rgb(68,68, 68);
    margin-top: 100px;
  }
  .no-project-hint span {
    text-decoration: underline;
  }
  .text {
    padding: 8px;
  }
  .add-project-status {
    border-top: 1px solid rgb(227, 227, 227);
    padding: 8px;
    font-size: 14px; 
  }
  .content .card {
    width: 22%;
    margin: 10px 1.5%;
    background: #fff;
    line-height: 1.7;
    border: 1px rgb(207,207,207) solid;
    float: left;
  }
  .content .card:hover{
    box-shadow: 0 0 5px 5px #ccc;
  }
</style>



