import Vue from 'vue'
import VueRouter from 'vue-router'
/** all link */
import Login from './views/Login'
import Home from './views/Home'
import Project from './views/Project.vue'
import AllPatients from './views/Allpatients.vue'
import PatientDetail from './views/Patientdetail.vue'
// import Records from './views/Records.vue'
import ProjectManagement from './views/Projectmanagement.vue'
import FollowPlan from './views/Followplan.vue'
import FollowDetail from './views/Followdetail.vue'
import Notification from './views/Notification.vue'
import UserInfo from './views/UserInfo.vue'
import CustomForm from './views/CustomForm.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: Home,
      meta: {requiresAuth: true}
    },
    {
      path: '/home',
      component: Home,
      meta: {requiresAuth: true}
    },
    {
      path: '/custom_form',
      component: CustomForm,
      meta: {requiresAuth: true}
    },
    {
      path: '/home/projectid/:id',
      component: Project,
      meta: {requiresAuth: true},
      children: [
        {
          path: '/home/projectid/:id/all_patients',
          component: AllPatients
        },
        {
          path: '/home/projectid/:id/all_patients/patient_detail/:MDID',
          component: PatientDetail/* ,
          children: [
            {
              path: '/home/projectid/:id/all_patients/patient_detail/:MDID/template/:templateid',
              component: Records
            }
          ] */
        },
        {
          path: '/home/projectid/:id/project_management',
          component: ProjectManagement
        },
        {
          path: '/home/projectid/:id/follow_plans',
          component: FollowPlan
        },
        {
          path: '/home/projectid/:id/follow_plans/follow_detail/:followid',
          component: FollowDetail
        }
      ]
    },
    {
      path: '/notification',
      component: Notification,
      meta: {requiresAuth: true}
    },
    {
      path: '/userInfo',
      component: UserInfo,
      meta: {requiresAuth: true}
    },
    { path: '/login', component: Login },
    { path: '*', component: Login }
  ]
})

export default router



