<template>
  <div>
      <div class="patientbase">
          <div class="patientname" v-text="patientBase.u_patientname"></div>
          <div class="patientcard">
            <p><span>性别:</span><span v-text="patientBase.u_gender == '' ? '' : patientBase.u_gender == '1' ? '男' : '女'"></span></p>
            <p><span>出生日期:</span><span v-text="patientBase.u_birthday"></span></p>
            <p><span>创建人:</span><span v-text="patientBase.s_username"></span></p>
            <p><span>创建时间:</span><span v-text="patientBase.u_createtime"></span></p>
            <p><span>所属项目:</span><span v-text="patientBase.u_projectName"></span></p>
          </div>
      </div> 
      <div class="patientrecords">
        <div class="recordsName">
          <span v-for="item in templates" v-text="item.u_templatename" @click="showPatientData(item.u_templateid)"></span>
        </div>
        <div class="recordsData">
          <keep-alive>
            <component v-bind:is="mycomponent" :patientid="patientid" :templateid="templateid"></component>          
          </keep-alive>
        </div>
      </div>   
  </div>
</template>
<script>
  import $ from 'webpack-zepto'
  import API from '../api.js'
  // import DialogBox from '../components/dialog.vue'
  import Vue from 'vue'
  import Temp from '../template/index'
  export default {
    data () {
      return {
        patientBase: {},
        templates: [],
        patientid: this.$route.params.MDID,
        templateid: '',
        mycomponent: ''
      }
    },
    created () {
      /* patientBase */
      API.GetPatientBase({mdid: this.$route.params.MDID}).then((response) => {
        this.patientBase = response[0]
      }).catch((err) => {
        window.alert(err)
      })
      /* projectTemplate */
      API.GetTemplates({projectid: this.$route.params.id}).then((response) => {
        this.templates = response
        this.templateid = this.templates[0].u_templateid
        Vue.component('mytemp', Temp['template' + this.templateid])
        this.mycomponent = 'mytemp'
        setTimeout(function () {
          $('.recordsName span').eq(0).addClass('active')
        }, 200)
      }).catch((err) => {
        window.alert(err)
      })
    },
    methods: {
      showPatientData (templateid, event) {
        let e = window.event || event
        $(e.target).addClass('active').siblings().removeClass('active')
        this.templateid = templateid
        this.mycomponent = 'mytemp'
        Vue.component('mytemp', Temp['template' + templateid])
      }
    }
  }
</script>
<style scoped>
  /* 基本信息 */
  .patientbase{
    width: 100%;
    display: flex;
    display: -wibkit-flex;
    position: relative;
    border: 1px #ccc solid;
    padding: 1em;
  }
  .patientname{
    width: 6em;
    margin-right: 1em;
    display: flex;
    display: -wibkit-flex;
    align-items: center;
    justify-content: center;
  }
  .patientcard{
    flex:1
  }
  .patientcard p{
    line-height: 1.7
  }
  /* 记录 */
  .recordsName{
    margin-top: 10px;
  }
  .recordsName span{
    padding: 5px;
    cursor: pointer;
  }
  .recordsName span.active{
    background: #20a0ff;
    color: #fff;
  }
  .recordsData{
    border: 1px #20a0ff solid;
    padding: 10px;
    font-size: 14px;
  }
</style>


